@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800');

@import url('https://fonts.googleapis.com/css?family=Source+Code+Pro:200,300,400,500,600,700,900');

/**************************************/

/*** Screen Sizes Mixins ***/

// 512
@mixin xs {
    @media (max-width: 511px) {
        @content;
    }
}

// 768
@mixin sm {
    @media (max-width: 799px) {
        @content;
    }
}

// 1024
@mixin md {
    @media (max-width: 1023px) {
        @content;
    }
}

// 1440
@mixin lg {
    @media (max-width: 1279px) {
        @content;
    }
}

/**************************************/

/*** Colours Variables ***/

/*main*/

$m-01: #698fb7;

/*neutrals*/

$neu-01: #f9f9f9;
$neu-02: #f0f0f0;
$neu-03: #ccc;
$neu-04: #848484;
$neu-05: #525252;
$neu-06: #4a4a4a;
$neu-07: #383838;

/**************************************/

/* --------------------------*/

/* ----- MAIN ELEMENTS ----- */

/* --------------------------*/

/* // General Elements \\  */

body {
    font-family: sans-serif;
    font-family: 'Open Sans', sans-serif;
    font-weight: 200;
    margin: 0;
    padding: 0;
    font-size: 15px;
    transition: all .3s ease-in-out;
}

#wrapper {
    width: 100%;
    margin: 0;
    padding: 0;
    position: relative;
    transition: all .3s ease-in-out;

}

/* // Selected Elements \\  */

::-moz-selection {
    background-color: $m-01;
    color: #ffffff;
}

::selection {
    background-color: $m-01;
    color: #ffffff;
}


/*  // Components \\  */

button {
    color: $neu-04;
    display: inline-block;
    padding: 15 15 15 18;
    text-align: center;
    letter-spacing: 5px;
    background-color: #f6f6f6;
    transition: all .6s ease;
    font-size: 12px;
    cursor: pointer;
    position: relative;
    z-index: 1;
    font-family: 'Source Code Pro', monospace;
    font-weight: 500;
    border: 0;
    margin: 0 auto;
    margin-top: 40;
    display: block;

    &:after {
        content: '';
        width: 0%;
        height: 2px;
        background-color: $m-01;
        border-radius: 20px;
        position: absolute;
        margin: 0 auto;
        bottom: 0;
        left: 50%;
        transition: all 0.8s ease;

    }

    &:before {
        content: '';
        position: absolute;
        background-color: #fff;
        width: 0%;
        height: 100%;
        left: 50%;
        z-index: -1;
        top: 0;
        transition: all 0.6s ease;
    }

    &:hover {
        color: $m-01;
        z-index: 1;

        @include sm {
            color: $neu-04;
        }

        &:after {
            width: 30%;
            left: calc(50% - 15%);

            @include sm {
                display: none;
            }
        }

        &:before {
            width: 100%;
            left: 0%;

            @include sm {
                display: none;
            }
        }
    }

    &:focus {
        outline: 0;
    }

}

a {
    text-decoration: none;
    color: inherit;
    cursor: pointer;
}

p {
    color: $neu-04;
    font-size: 16px;
    padding: 0;
    margin: 0;
    line-height: 1.8;
    letter-spacing: 0.7;
    transition: all 0.3s ease;

    @include sm {
        font-size: 14px;
    }
}

.links {
    color: $m-01;
    position: relative;
    font-family: 'Source Code Pro', monospace;
    font-weight: 300;
    display: flex;
    justify-content: center;

    &:after {
        content: '';
        width: 0%;
        height: 2px;
        border-radius: 20px;
        background-color: $m-01;
        position: absolute;
        bottom: -2px;
        transition: all 0.3s ease;

        @include sm {
            width: 0;
            text-decoration: underline;
        }
    }

    &:hover {
        &:after {
            width: 100%;
        }
    }
}

/*  // Texts \\  */

.main-title {
    font-size: 35px;
    color: $neu-04;
    font-family: 'Open Sans', sans-serif;
    font-weight: 100;
    letter-spacing: 10px;
    text-align: center;
    margin: 0;
    margin-bottom: 60px;
    text-transform: uppercase;
    transition: all .5s ease;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;

    &.white {
        color: #fff;
    }

    @include sm {
        font-size: 25px;
        line-height: 35px;
    }

    &:after {
        background-color: $m-01;
        width: 80px;
        content: '';
        height: 2px;
        border-radius: 20px;
        position: absolute;
        transition: all .5s ease;
        bottom: -20px;

        @include sm {
            width: 50px;
        }
    }

    &:hover {
        &:after {
            width: 100px;

            @include sm {
                width: 50px;
            }
        }
    }
}

.main-text {
    max-width: 800px;
    text-align: center;
    margin: 0 auto;

    @include sm {
        padding: 0 10px;
    }

    &.left {
        text-align: left;
    }
}

/*  // Back to Top \\  */

#scrollme {
    margin: 0 auto;
    height: 50px;
    cursor: pointer;
    width: 120px;

    .backtop {
        position: relative;
        color: $m-01;

        p {
            font-size: 12px;
            font-family: 'Source Code Pro', monospace;
            font-weight: 400;
            letter-spacing: 2px;
            margin-bottom: 50px;
            opacity: 0;
            transition: all 0.6s ease;
            padding-top: 10px;
            color: $m-01;
            text-align: center;
            margin: 0 auto;
            cursor: pointer;
        }

        span {
            width: 20px;
            height: 2px;
            position: absolute;
            top: 20px;
            left: calc(50% - 10px);
            border-radius: 20px;
            transition: all 0.4s ease-in-out;
            background-color: $m-01;
        }

        span:nth-child(1) {
            transform: rotate(45deg);
            margin-left: 6px;
        }

        span:nth-child(2) {
            transform: rotate(-45deg);
            margin-left: -7px;
        }
    }

    &:hover {
        .backtop {
            p {
                opacity: 1;
                padding-top: 0px;
                font-size: 11px;

                @include sm {
                    display: none;
                }
            }

            span {
                top: 22px;
                width: 20px;

                @include sm {
                    top: 20px;
                }
            }

            span:nth-child(1) {
                left: calc(50% - 5px);
                transform: rotate(180deg);

                @include sm {
                    left: calc(50% - 10px);
                    transform: rotate(45deg);
                    width: 20px;
                    margin-left: 6px;
                }
            }

            span:nth-child(2) {
                right: calc(50% - 5px);
                transform: rotate(-180deg);

                @include sm {
                    right: calc(50% - 10px);
                    transform: rotate(-45deg);
                    width: 20px;
                    margin-left: -7px;

                }
            }
        }
    }
}

/* ---------------------------*/

/* --------- GENERAL -------- */

/* ---------------------------*/

.main-nav {

    .iconmenu {
        width: 40px;
        height: 40px;
        margin: 25px;
        position: fixed;
        z-index: 4;
        cursor: pointer;

        @include sm {
            margin: 10px;
        }

        .iconopen {
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 100%;
            background-color: $neu-01;
            border-radius: 100%;

            span {
                position: absolute;
                width: 20px;
                height: 2px;
                border-radius: 20px;
                transition: all 0.3s ease;
                background-color: $m-01;
                display: flex;
                align-items: center;
                justify-content: center;

                &:before,
                &:after {
                    position: absolute;
                    content: '';
                    width: 20px;
                    height: 100%;
                    background-color: inherit;

                }

                &:before {
                    transition: all 0.3s ease;
                    transform: translateY(-6px);
                }

                &:after {
                    transition: all 0.4s ease;
                    transform: translateY(6px);
                }
            }

            &.close {
                transition: all 0.3s ease;
                background-color: transparent;

                span {
                    background-color: $neu-03;
                    width: 0px;

                    &:before {
                        width: 20px;
                        transform: rotate(45deg);
                    }

                    &:after {
                        width: 20px;
                        transform: rotate(-45deg);
                    }
                }
            }
        }

        &:hover {
            .iconopen {
                span {
                    &:before {
                        width: 16px;

                        @include sm {
                            width: 20px;
                        }
                    }

                    &:after {
                        width: 16px;

                        @include sm {
                            width: 20px;
                        }
                    }
                }

                &.close {
                    span {
                        background-color: $neu-04;

                        @include sm {
                            background-color: $neu-03;
                        }

                        &:before {
                            width: 20px;
                        }

                        &:after {
                            width: 20px;
                        }
                    }
                }
            }
        }
    }

    .nav {
        background-color: white;
        margin: 0;
        width: 100%;
        height: 90px;
        position: fixed;
        top: -95px;
        transition: all .5s ease-in;
        box-shadow: 0px -10px 20px black;
        z-index: 3;
        display: flex;
        align-items: center;
        justify-content: center;

        @include sm {
            top: -65px;
            height: 60px;
        }

        &.navshow {
            transition: all .5s ease-out;
            top: 0;
        }

        /*  // Menu List \\  */
        .menu {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-wrap: wrap;
            width: 100%;

            .menu-item {
                color: $neu-04;
                list-style: none;
                width: 20%;
                min-width: 60px;
                max-width: 250px;
                letter-spacing: 4px;
                height: 50px;
                transition: all 0.4s ease;
                position: relative;
                font-family: 'Source Code Pro', monospace;
                font-weight: 400;
                font-size: 14px;
                display: flex;
                align-items: center;
                justify-content: center;

                @include sm {
                    min-width: 60px;
                    font-size: 13px;
                    letter-spacing: 2px;
                    height: 30px;
                }

                &:after {
                    content: '';
                    width: 0%;
                    height: 2px;
                    border-radius: 20px;
                    background-color: $m-01;
                    position: absolute;
                    bottom: 0;
                    transition: all 0.4s ease;
                }

                &:hover {
                    color: $m-01;

                    @include sm {
                        letter-spacing: 2px;
                        color: $neu-04;
                    }

                    &:after {
                        width: 35px;

                        @include sm {
                            width: 0px;
                        }
                    }
                }

                &.current {
                    color: $m-01;

                    &:after {
                        width: 30px;

                        @include sm {
                            width: 20px;
                        }

                    }
                }
            }
        }
    }

}

.footer {
    background-color: #313131;
    height: 120px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    @include sm {
        height: 100px;
    }

    p {
        font-size: 12px;
        letter-spacing: 1.1;
        font-family: 'Source Code Pro', monospace;
        font-weight: 400;
        text-align: center;
        color: $neu-02;

        &:first-child {
            color: $neu-04;
        }
    }
}

.content {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: calc(100% - 10%);
    margin: 0 auto;
    transition: all .3s ease-in-out;
}

.content-full {
    padding: 0 5%;

    &.blue {
        background-color: $m-01;
    }

    &.gray {
        background-color: $neu-02
    }

    &.dark {
        background-color: $neu-07;
    }
}

.section,
.section-works {
    width: 100%;
    max-width: 1280px;
    padding: 100px 0;
    transition: all .3s ease-in-out;
    margin: auto;

    @include sm {
        padding: 50px 0;
    }
}


/* ---------------------------*/

/* --------- MODULES -------- */

/* ---------------------------*/

.header {
    width: 100%;
    margin: 0 auto;
    height: 500px;
    transition: all 0.6s ease-in-out;
    background-color: $neu-03;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;


    @include sm {
        height: 360px;
        background-size: 250px, cover;
        background-position: center 50px;
    }

    &:before {
        background-color: $m-01;
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: 0;
    }

    span {
        position: absolute;
        top: 0;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        width: 25%;
        height: 100%;
        opacity: 0.5;
        transition: width 0.6s ease;
        z-index: 1;
        -webkit-filter: grayscale(100%);
        /* Safari 6.0 - 9.0 */
        filter: grayscale(100%);

        &:nth-child(1) {
            left: 0;
        }

        &:nth-child(2) {
            left: 25%;
        }

        &:nth-child(3) {
            right: 25%;
        }

        &:nth-child(4) {
            right: 0%;
        }

        &:after {
            position: absolute;
            width: 100%;
            height: 100%;
            content: '';
            left: 0;
            top: 0;
            background-color: rgba(52, 52, 52, 0.85);
            transition: all 0.4s ease-in;
        }

        &:hover {
            width: 40%;
            opacity: 1;
            -webkit-filter: grayscale(0%);
            filter: grayscale(0%);
            z-index: 2;

            @include sm {
                width: 25%;
                -webkit-filter: grayscale(100%);
                filter: grayscale(100%);
                opacity: 0.5;
            }

            &:after {
                background-color: rgba(52, 52, 52, 0.5);

                @include sm {
                    background-color: rgba(52, 52, 52, 0.85);
                }

            }
        }
    }

    .header-content {
        width: 100%;
        z-index: 2;
        position: relative;
        transition: all .6s ease-in-out;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        h1 {
            font-size: 50px;
            color: white;
            font-weight: 100;
            letter-spacing: 2px;
            -webkit-transition: all .6s ease-in-out;
            transition: all .6s ease-in-out;

            @include sm {
                font-size: 35px;
            }
        }

        p {
            color: white;
            font-family: 'Source Code Pro', monospace;
            font-weight: 400;
            letter-spacing: 8;
            font-size: 14px;
            text-align: center;
            z-index: 2;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;

            &:before {
                content: '';
                width: 160px;
                height: 6px;
                position: absolute;
                background-color: $m-01;
                z-index: -1;
                transform: translateX(-4px);
            }
        }
    }

    &:hover {
        .header-content {
            opacity: 0.2;

            @include sm {
                opacity: 1;
            }
        }
    }
}

.works-container {

    .image-container {
        display: grid;
        grid-template-columns: 50% 50%;

        @include sm {
            grid-template-columns: 100%;
        }

        /*  // IE CODE \\  */
        @media all and (-ms-high-contrast: none),
        (-ms-high-contrast: active) {
            display: flex;
            flex-direction: column;
        }

    }

    .image {
        position: relative;
        width: calc(100% - 20px);
        padding-bottom: calc(100% - 20px);
        margin-bottom: 10px;
        margin-top: 10px;
        background-size: cover;
        background-position: 50%;
        transition: all .5s ease;
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;

        &.big {
            padding-bottom: 50%;

            @include sm {
                width: 100%;
                padding-bottom: 100%;
            }
        }

        @include sm {
            width: 100%;
            padding-bottom: 100%;
        }

        /* filtro cinza */
        &:before {
            content: '';
            opacity: 0.1;
            background-color: #000;
            cursor: pointer;
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            transition: all .4s ease-in-out;
        }

        /* filtro azul */
        &:after {
            position: absolute;
            content: '';
            opacity: 0;
            background-color: $m-01;
            transition: all .4s ease-in-out;
            cursor: pointer;
            width: 90%;
            height: 90%;
            z-index: 1;
            top: 5%;
        }

        .overlay-title {
            width: 100%;
            padding: 0 40px;
            box-sizing: border-box;
            font-size: 18px;
            line-height: 20px;
            position: absolute;
            font-weight: 500;
            top: 42%;
            opacity: 0;
            z-index: 2;
            text-align: center;
            cursor: pointer;
            transition: all .5s ease;
            color: white;

            @include sm {
                display: none;
            }
        }

        .overlay-text {
            width: 100%;
            padding: 0 40px;
            box-sizing: border-box;
            font-size: 14px;
            line-height: 20px;
            position: absolute;
            top: 60%;
            opacity: 0;
            z-index: 2;
            text-align: center;
            font-family: 'Source Code Pro', monospace;
            font-weight: 300;
            transition: all .9s ease;
            margin-top: 10px;
            color: white;

            @include sm {
                display: none;
            }

            span {
                opacity: 0.4;
            }
        }

        &:hover {

            /* filtro cinza */
            &:before {
                opacity: 0.5;

                @include sm {
                    opacity: 0.1;
                }
            }

            /* filtro azul */
            &:after {
                opacity: 0.9;
                width: 100%;
                height: 100%;
                top: 0;

                @include sm {
                    display: none;
                }
            }

            .overlay-title {
                opacity: 1;
                top: 48%;
            }

            .overlay-text {
                opacity: 0.7;
                top: 55%;
            }
        }


    }
}

.info-icons {
    .row-icons {
        display: grid;
        grid-template-columns: 16% 16% 16% 16% 16% 16%;
        -webkit-transition: all .3s ease-in-out;
        transition: all .3s ease-in-out;

        @include md {
            grid-template-columns: 33% 33% 33%;

        }

        @include sm {
            grid-template-columns: 50% 50%;
        }

        .icon-section {
            width: 120px;
            text-align: center;
            margin: 0 auto;
            position: relative;
            z-index: 1;

            &:before {
                background-color: white;
                position: absolute;
                content: '';
                border-radius: 1px;
                z-index: 1;

                @include sm {
                    background-color: $m-01;
                }
            }

            &:after {
                background-color: $m-01;
                position: absolute;
                content: '';
                transition: all 0.4s ease-in-out;
                border-radius: 1px;
                z-index: 2;
            }

            p {
                font-size: 12px;
                line-height: 20px;
                margin: 15 0 0 0;
                letter-spacing: 0px;
                font-family: 'Source Code Pro', monospace;
                font-weight: 400;

                @include sm {
                    font-size: 11px;
                    line-height: 18px;
                    margin: 10 0 0 0;
                    color: $neu-04;
                }
            }

            &:hover {
                p {
                    color: $m-01;

                    @include sm {
                        font-size: 11px;
                        line-height: 18px;
                        margin: 10 0 0 0;
                        color: $neu-04;
                    }
                }
            }

            &:nth-child(1) {
                &:before {
                    width: 13px;
                    height: 12px;
                    top: 70px;
                    left: 37px;
                }

                &:after {
                    width: 0px;
                    height: 12px;
                    top: 70px;
                    left: 37px;
                }

                &:hover {
                    &:after {
                        width: 13px;
                    }
                }

            }

            &:nth-child(2) {
                &:before {
                    width: 52px;
                    height: 9px;
                    top: 87px;
                    left: 25px;
                }

                &:after {
                    width: 0px;
                    height: 9px;
                    top: 87px;
                    left: 25px;
                }

                &:hover {
                    &:after {
                        width: 52px;
                    }
                }
            }

            &:nth-child(3) {
                &:before {
                    width: 17px;
                    height: 27px;
                    top: 58px;
                    left: 66px;
                }

                &:after {
                    width: 17px;
                    height: 0px;
                    top: 58px;
                    left: 66px;
                }

                &:hover {
                    &:after {
                        height: 27px;
                    }
                }
            }

            &:nth-child(4) {
                &:before {
                    width: 17px;
                    height: 17px;
                    top: 71px;
                    left: 49px;
                    z-index: -2;
                }

                &:after {
                    width: 17px;
                    height: 0px;
                    top: 71px;
                    left: 49px;
                    z-index: -1;
                }

                &:hover {
                    &:after {
                        height: 17px;
                    }
                }
            }

            &:nth-child(5) {
                &:before {
                    width: 21px;
                    height: 19px;
                    top: 88px;
                    left: 22px;
                }

                &:after {
                    width: 0px;
                    height: 19px;
                    top: 88px;
                    left: 22px;
                }


                &:hover {
                    &:after {
                        width: 21px;
                    }
                }
            }

            &:nth-child(6) {
                &:before {
                    width: 12px;
                    height: 12px;
                    top: 81px;
                    left: 62px;
                    border-radius: 50px;
                }

                &:after {
                    width: 0px;
                    height: 0px;
                    top: 87px;
                    left: 68px;
                    border-radius: 50px;
                }

                &:hover {
                    &:after {
                        width: 12px;
                        height: 12px;
                        top: 81px;
                        left: 62px;
                    }
                }
            }
        }



    }
}

.contacts {
    display: flex;
    justify-content: space-around;
    text-align: center;
    transition: all 0.8s ease;
    -webkit-transition: all .8s ease;

    /* social item*/
    .social {
        background-color: $neu-06;
        padding: 100 0;
        z-index: 2;
        width: 100%;
        position: relative;
        transition: all 0.6s ease;
        display: flex;
        align-items: center;
        justify-content: center;


        /*  // Social Texts \\  */
        .social-title {
            color: $neu-03;
            letter-spacing: 10px;
            font-size: 14px;
            position: absolute;
            letter-spacing: 4px;
            width: 100%;
            transition: all 0.9s ease;
            transform: translateY(-30px);
            opacity: 0;
            font-family: 'Source Code Pro', monospace;
            font-weight: 400;

            @include sm {
                transform: translateY(30px);
                opacity: 1;
                letter-spacing: 2;
                font-size: 11px;
                color: $neu-03;
            }
        }

        .social-link {
            font-family: 'Source Code Pro', monospace;
            font-weight: 400;
            font-size: 12px;
            opacity: 0;
            color: white;
            transition: all 0.7s ease;
            position: absolute;
            width: 100%;
            transform: translateY(30px);

            @include sm {
                display: none;
            }
        }

        /*  // Icon Social \\  */
        span {
            width: 60px;
            height: 60px;
            position: absolute;
            opacity: 1;
            transition: all 0.4s ease;
            background-size: contain;
            background-repeat: no-repeat;
            background-position: center;

            @include sm {
                width: 40px;
                height: 40px;
            }
        }

        /*  // Background Icons \\  */
        &:nth-child(1) {
            span {
                background-image: url("../images/icons/icons_mail.svg");
            }
        }

        &:nth-child(2) {
            background-color: $neu-05;

            span {
                background-image: url("../images/icons/icons_behance.svg");
            }
        }

        &:nth-child(3) {
            span {
                background-image: url("../images/icons/icons_linkedin.svg");
            }
        }

        &:before {
            content: '';
            position: absolute;
            background-color: #313131;
            width: 0%;
            height: 100%;
            z-index: -1;
            top: 0;
            transition: all 0.7s ease;
            color: $neu-03;
        }

        &:after {
            content: '';
            width: 0%;
            height: 2px;
            border-radius: 20px;
            background-color: $m-01;
            position: absolute;
            bottom: 0;
            transition: all 0.9s ease;
        }

        @include sm {
            padding: 50 0;
        }

        &:hover {
            color: $m-01;
            letter-spacing: 6px;
            text-align: center;

            &:before {
                width: 100%;

                @include sm {
                    display: none;
                }
            }

            &:after {
                width: 100%;

                @include sm {
                    display: none;
                }
            }

            .social-title {
                opacity: 1;
                transform: translateY(-15px);
                letter-spacing: 6px;

                @include sm {
                    transform: translateY(30px);
                    opacity: 1;
                    letter-spacing: 2;
                    font-size: 11px;
                    color: $neu-03;
                }
            }

            .social-link {
                opacity: 0.2;
                letter-spacing: 1;
                transform: translateY(15px);

                @include sm {
                    display: none;
                }
            }

            span {
                width: 50%;
                height: 50%;
                opacity: 0.03;

                @include sm {
                    width: 40px;
                    height: 40px;
                    top: 20%;
                    opacity: 1;

                }
            }
        }
    }
}

