.bio-content {
    max-width: 1024px;
    display: grid;
    grid-template-columns: 40% 50%;
    margin: 0 auto;

    @include sm {
        grid-template-columns: 100%;
    }

    @media all and (-ms-high-contrast: none),
    (-ms-high-contrast: active) {
        display: flex;
        justify-content: space-around;

        @include sm {
            display: flex;
            justify-content: space-around;
            flex-direction: column;
        }
    }

    .bio {
        font-size: 15px;
        position: relative;
        z-index: 2;
        text-align: left;
        margin-left: 60px;

        @include sm {
            margin-left: 0px;
            font-size: 13px;
        }

        h2 {
            font-size: 35px;
            color: $neu-04;
            font-family: 'Open Sans', sans-serif;
            font-weight: 100;
            letter-spacing: 10px;
            z-index: 2;
            text-transform: uppercase;
            -webkit-transition: all .5s ease;
            transition: all .5s ease;
            max-width: 500px;
            text-align: left;
            margin: 20px 0px 50px -100px;

            @include sm {
                margin: 0px 0px 40px 0px;
                font-size: 25px;
                line-height: 35px;
            }

            &:after {
                background-color: $m-01;
                width: 80px;
                content: '';
                height: 2px;
                border-radius: 20px;
                display: block;
                margin-top: 20px;
                transition: all .5s ease;
                left: 0;

                @include sm {
                    width: 50px;
                }
            }
        }
    }

    .image-about {
        width: 100%;
        height: 500px;
        z-index: 2;
        transition: all .6s ease-in-out;
        opacity: 0.2;
        background-image: url("../images/bio2.png");
        background-size: cover;
        -webkit-filter: grayscale(100%);
        filter: grayscale(100%);

        @include sm {
            height: 250px;
            margin-bottom: 40px;
        }
    }
}

.tab-navigation {

    .tab {
        overflow: hidden;
        display: flex;
        justify-content: space-around;
        margin: 0 auto;
        margin-bottom: 50px;
        transition: all .6s ease-in-out;

        span {
            font-weight: 300;
            letter-spacing: 6px;
            text-transform: uppercase;
            font-size: 20px;
            padding: 14px 0px;
            color: #fff;
            transition: all .6s ease-in-out;
            width: 200px;
            position: relative;
            cursor: pointer;
            opacity: 0.5;
            display: flex;
            justify-content: center;

            @include sm {
                font-size: 15px;
                letter-spacing: 4px;
                width: 130px;
            }

            &:after {
                content: '';
                width: 0%;
                height: 2px;
                border-radius: 20px;
                background-color: #fff;
                position: absolute;
                bottom: 0;
                transition: all 0.6s ease;
            }

            &.active {
                opacity: 1;

                &:after {
                    width: 40px;
                }
            }
        }

        &:hover {
            span {
                opacity: 1;

                &:after {
                    width: 40px;
                }
            }
        }

    }


    .tabcontent {
        margin: 0 auto;
        display: none;
        padding: 6px 12px;
        top: 10;
        transition: all 0.5s ease;
    }

    .skills-section {
        display: flex;
        justify-content: space-around;
        flex-flow: wrap;
        transition: all .3s ease-in-out;

        @include sm {
            flex-direction: column;
        }

        p {
            font-size: 14px;
            text-align: center;
            color: #fff;
            margin: 10px;
            transition: all 0.5s ease;
            font-family: 'Source Code Pro', monospace;
            font-weight: 300;
            letter-spacing: 0;
        }
    }

    #Skills {
        display: block;
    }
}

.timeline-section {
    margin: 0 auto;
    text-align: center;
    display: grid;
    grid-template-columns: calc(50% - 30px) calc(50% - 30px);
    max-width: 700px;
    margin-top: 0px;
    margin-bottom: 90px;
    column-gap: 60px;

    &:last-child {
        margin-bottom: 0;
    }

    @include sm {
        grid-template-columns: 100%;
        padding: 0;
        margin: 0 5%;
        margin-bottom: 60px;
    }

    /*  // IE CODE \\  */
    @media all and (-ms-high-contrast: none),
    (-ms-high-contrast: active) {
        display: flex;
        justify-content: space-around;
    }

    .timeline {
        margin-bottom: 50px;
        text-align: left;
        padding: 0;
        position: relative;

        @include sm {
            &:last-child {
                margin-bottom: 0px;
            }
        }

        &:after {
            background-color: #f6f6f6;
            position: absolute;
            height: calc(100% - 25px);
            width: 90%;
            content: '';
            right: 0;
            bottom: 0;
            z-index: -1;
            display: block;
        }

        label {
            color: $m-01;
            font-size: 14px;
            text-align: left;
            font-family: 'Source Code Pro', monospace;
            font-weight: 400;
            position: relative;
            display: block;
            z-index: 2;
            padding: 0 0 15 0;

            @include sm {
                padding: 0 0 10 0;
                font-size: 14px;
            }

        }

        p {
            font-size: 14px;
            padding: 0 15 15 0;
            text-align: left;

            strong {
                letter-spacing: 1px;
                font-weight: 600;
                position: relative; //color: white;
                z-index: 1;
            }

            em {
                color: $neu-03;
                text-decoration: none;
            }
        }
    }
}

